import FilterHeader from "@/components/FilterHeader";
import { Card, Collapse, CollapseProps, Divider, Drawer, Space, Typography } from "antd";
import { useState } from "react";
import JSErrorList from "./jsErrorList";
import { ClockCircleOutlined, CloseOutlined } from "@ant-design/icons";
import { getJSErrorInfo } from "@/api/modules/web/jserror";
import { ResJSError } from "@/api/interface/web/jserror";
import Paragraph from "antd/es/typography/Paragraph";
import Title from "antd/es/typography/Title";

const Dashboard: React.FC = () => {
    const [jsErrorInfo, setJsErrorInfo] = useState<ResJSError>();
    const [open, setOpen] = useState(false);

    const items: CollapseProps['items'] = [
        {
            key: '1',
            label: '堆栈明细',
            children: <p>{jsErrorInfo && jsErrorInfo.stack}</p>,
        },
    ];

    const onClose = () => {
        setOpen(false);
    };

    const getJSError = async (id: string) => {
        const res = await getJSErrorInfo(id);
        res.data && setJsErrorInfo(res.data);
    }

    const showDrawer = (key: string) => {
        getJSError(key);
        setOpen(true);
    };

    return (<div>
        <FilterHeader></FilterHeader>
        <Card title="错误列表" style={{ marginTop: '16px' }}>
            <JSErrorList onClick={showDrawer}></JSErrorList>
        </Card>
        <Drawer
            title="错误详情"
            placement='right'
            closable={false}
            width='80%'
            onClose={onClose}
            open={open}
            extra={
                <Space>
                    <CloseOutlined onClick={onClose} />
                </Space>
            }
        >
            <div>
                <Typography>
                    <Title level={4}>{jsErrorInfo?.name}</Title>
                    <Paragraph>
                        {jsErrorInfo?.msg}
                    </Paragraph>
                    <Paragraph>
                        <ClockCircleOutlined />&nbsp;&nbsp;{jsErrorInfo?.createdAt}
                    </Paragraph>
                </Typography>
                <Divider />
                <Typography>
                    <Title level={5}>Js错误堆栈</Title>
                    <Paragraph>
                        {jsErrorInfo?.msg}
                    </Paragraph>
                </Typography>
                <Collapse bordered={false} items={items} defaultActiveKey={['1']} />
            </div>
        </Drawer>
    </div>);
}

export default Dashboard;